<template>
    <div class="container">
        <v-back title="通行统计" />
        <div class="box">
			<md-field>
                <md-field-item title="选择日期" :content="selectorValue1" @click="showSelector1" arrow solid />
                <md-field-item title="选择宿舍楼" :content="selectorValue2" @click="showSelector2" arrow solid />
			</md-field>
		</md-field>
        <md-date-picker
            ref="datePicker"
            v-model="isSelectorShow1"
            type="custom"
            title="选择日期"
            large-radius
            :custom-types="['yyyy', 'MM','dd']"
            :default-date="currentDate"
            @confirm="onSelectorConfirm1" ></md-date-picker>
        <md-selector v-model="isSelectorShow2" :data="data2" @confirm="onSelectorConfirm2" min-height="320px" max-height="320px" title="选择宿舍楼" okText="确认" large-radius></md-selector>
            <h2><md-notice-bar style="color: #1acfc6;">通行人群</md-notice-bar></h2>
            <div class="flex align_center peopel space-between">
            	<div v-for="(item, index) in personData.rows" :key="index" @click="todo(item['身份'])"><i class="md-icon icon-font md-icon-search search"></i> {{item['身份']}}</div>
            </div>
			<ve-histogram :events="tenDepChartEvents" :data="personData" :extend="histogramChartExtend" :legend-visible="true"></ve-histogram>
            
			<h2><md-notice-bar style="color: #1acfc6;">通行时段</md-notice-bar></h2>
			<div class="flex space-between align_center btns_box">
				<div class="btn btn1">
					<md-field-item
					  class="select"
					  :content="selectorValue"
					  @click="showSelector"
					  arrow
					/>
				</div>
				<div class="btn btn2" @click="todo(selectorValue)"><i class="md-icon icon-font md-icon-search search"></i> 通行记录</div>
			</div>
			<md-selector
			    v-model="isSelectorShow"
			    :data="data"
			    min-height="320px"
			    max-height="320px"
			    title="搜索类型"
			    okText="确认"
			    large-radius
			    @confirm="onSelectorConfirm"
			    >
			</md-selector>
            <ve-line :data="chartData" :extend="histogramChartExtend"></ve-line>
        </div>
    </div>
</template>

<script>
    import VeHistogram from 'v-charts/lib/histogram'
    import VeLine from 'v-charts/lib/line'
    import dayjs from 'dayjs'
    export default {
        name: 'traffic_statistics',
        data() {
            return {
                selectorValue1: '',
                selectorValue2: '',
                isSelectorShow1: false,
                isSelectorShow2: false,
                currentDate: new Date(),
                value1: '',
                value2: '',
                data2: [],
                personData: {
                    columns: [],
                    rows: []
                },
                chartData: {
                    columns: [],
                    rows: []
                },
				histogramChartExtend: {
					label: {
						show: true,
						fontSize: 15
					},
					minInterval: 1 //最小间距是1
				},
				tenDepChartEvents: {
					click: (e) => {
						this.todo(e.name)
					}
				},
				
				
				selectorValue: '全部',
				selectorActive: '0',
				isSelectorShow: false,
				data: [
					{
						value: '0',
						text: '全部'
					},
					{
						value: 'S',
						text: '学生'
					},
					{
						value: 'T',
						text: '教师'
					},
					{
						value: 'V',
						text: '访客'
					},
					{
						value: 'Y',
						text: '黄名单'
					},
					{
						value: 'I',
						text: '陌生人'
					}
				]
            }
        },
        components: {
            VeHistogram,
            VeLine
        },
        methods: {
			todo(item) {
				let usertype = ''
				if (item == '全部') {
					usertype = ''
				}
				if (item == '学生') {
					usertype = 'S'
				}
				if (item == '教师' || item == '教职工') {
					usertype = 'T'
				}
				if (item == '访客') {
					usertype = 'V'
				}
				if (item == '黄名单') {
					usertype = 'Y'
				}
				if (item == '陌生人') {
					usertype = 'I'
				}
				console.log(usertype)
				this.$router.push('/traffic_statistics/list?usertype=' + usertype + '&entryExitTime=' + this.value1 + '&buildingId=' + this.value2)
			},
			showSelector() {
			    this.isSelectorShow = true
			},
			onSelectorConfirm({text, value}) {
				this.selectorValue = text
				this.selectorActive = value
				this.getData()
			},
            //获取宿舍楼栋
            getFloor() {
                this.$getL('/dorm/floor', {
					userId: JSON.parse(localStorage.getItem('user')).user_id
				}, (res)=>{
                    this.data2 = []
                    res.data.forEach((item, index) => {
                        this.data2.push({
                            value: item.buildingId,
                            text: item.buildingName
                        })
                        if (index == 0) {
                            this.selectorValue2 =item.buildingName
                            this.value2 = item.buildingId
                        }
                    })
                    this.getData()
				})
            },
            showSelector1() {
                this.isSelectorShow1 = true
            },
            showSelector2() {
                this.isSelectorShow2 = true
            },
            onSelectorConfirm1() {
                this.value1 = this.$refs.datePicker.getFormatDate('yyyy-MM-dd')
                this.selectorValue1 = this.$refs.datePicker.getFormatDate('yyyy-MM-dd')
                this.getData()
            },
             onSelectorConfirm2({text, value}) {
                this.value2 = value
                this.selectorValue2 = text
                this.getData()
            },
            getData() {
                //ajax todo
                //获取通行人群
				
                this.$getL('/traffic_statistics/people', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id,
                    entryExitTime: this.value1,
                    buildingId: this.value2,
					usertype: this.selectorActive == '0' ? '' : this.selectorActive
                }, (res)=>{
                    this.personData = {...res.data}
                    this.chartData = {...res.data2}
                    this.$toast.hide()
                })
            }
        },
        mounted() {
            this.selectorValue1 = dayjs(new Date()).format('YYYY-MM-DD')
            this.value1 = dayjs(new Date()).format('YYYY-MM-DD')
            this.getFloor()
        }
    }
</script>

<style scoped>
    .container {
        background-color: #fff;
    }
    .box {
        height: calc(100vh - 6vw);
        overflow-y: auto;
    }
    h2 {
        padding: 4vw 0;
        font-size: 3.5vw;
    }
	.btns_box {
		padding: 0 4vw;
		font-size: 3vw;
	}
	.btn1 {
		box-shadow: 2px 2px 5px rgba(0,0,0,.2);
	}
	.btn2 {
		background-color: #1acfc6;
		color: #fff;
		padding: 2.4vw 4vw;
		border-radius: 1vw;
		box-shadow: 2px 2px 5px rgba(0,0,0,.2);
	}
	.select.md-field-item {
		width: 24vw;
		background-color: #1acfc6;
		border-radius: 1vw;
		overflow: hidden;
	}
	.select.md-field-item >>> .md-field-item-content {
		padding: 0 2vw;
		min-height: 0;
		padding: 2.2vw 4vw;
	}
	.select.md-field-item >>> .md-field-item-right .md-icon-arrow-right {
		transform: rotate(90deg);
		color: #fff;
	}
	.select.md-field-item >>> .md-field-item-control {
		color: #fff;
		font-size: 3vw;
	}
	.peopel {
		padding: 0 4vw;
	}
	.peopel div {
		background-color: #1acfc6;
		color: #fff;
		padding: 2vw;
		border-radius: 1vw;
		box-shadow: 2px 2px 5px rgba(0,0,0,.2);
	}
</style>